<template>
  <el-card class="card">
    <!--  -->
    <my-bread level1="" level2=""></my-bread>
    <!--  -->
    <el-row class="row">
      <el-col>
        <el-alert title="" type="info" :closable="false" center show-icon></el-alert>
      </el-col>
    </el-row>
    <!--  -->
    <el-steps :active="active-0" align-center>
      <el-step title=""></el-step>
      <el-step title=""></el-step>
      <el-step title=""></el-step>
      <el-step title=""></el-step>
      <el-step title=""></el-step>
    </el-steps>
    <!--  -->
    <el-form label-position="top" :model="form">
      <!--  -->
      <el-tabs tab-position="left" v-model="active" @tab-click="changeTab()">
        <el-tab-pane label="" name="1">
          <el-form-item label="">
            <el-input v-model="form.goods_name"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="form.goods_price"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="form.goods_weight"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="form.goods_number"></el-input>
          </el-form-item>
          <!--  -->
          <el-form-item label="">
            <el-cascader
              expand-trigger="hover"
              clearable
              :options="options"
              v-model="selectedOptions"
              :props="defaultProps"
              @change="handleChange"
            ></el-cascader>
          </el-form-item>
        </el-tab-pane>
        <el-tab-pane label="" name="2">
          <el-form-item v-for="(item,i) in manyData" :key="i" :label="item.attr_name">
            <el-checkbox-group :min="1" v-model="item.attr_vals">
              <el-checkbox v-for="(v,i) in item.attr_vals" :key="i" border :label="v"></el-checkbox>
            </el-checkbox-group>
          </el-form-item>
        </el-tab-pane>
        <el-tab-pane label="" name="3">
          <el-form-item :label="item.attr_name" v-for="(item,i) in onlyData" :key="i">
            <el-input v-model="item.attr_vals" placeholder=""></el-input>
          </el-form-item>
        </el-tab-pane>
        <el-tab-pane label="" name="4">
          <el-form-item>
            <el-upload
              class="upload-demo"
              action="http://localhost:8888/api/private/v1/upload"
              :on-success="handleSuccess"
              :on-remove="handleRemove"
              :headers="headers"
              list-type="picture"
            >
              <el-button size="small" type="primary"></el-button>
              <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
            </el-upload>
          </el-form-item>
        </el-tab-pane>
        <el-tab-pane label="" name="5">
          <el-form-item>
            <el-button size="small" type="primary" @click="addGoods()"></el-button>
            <quill-editor></quill-editor>
          </el-form-item>
        </el-tab-pane>
      </el-tabs>
    </el-form>
  </el-card>
</template>

<script>
import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";
import "quill/dist/quill.bubble.css";

import { quillEditor } from "vue-quill-editor";

export default {
  components: {
    quillEditor
  },
  data() {
    return {
      active: 0,
      form: {
        goods_name: "",
        goods_price: "",
        goods_number: "",
        goods_weight: "",
        goods_introduce: "",
        goods_cat: "",
        pics: [],

        attrs: []
      },
      defaultProps: {
        value: "cat_id",
        label: "cat_name",
        children: "children"
      },
      options: [],
      selectedOptions: [1, 3, 6],
      manyData: [],
      onlyData: [],
      headers: {
        Authorization: localStorage.getItem("token")
      }
    };
  },
  methods: {
    //
    async addGoods() {
      this.form.goods_cat = this.selectedOptions.join(",");
      const activeData = this.manyData;
      const staticData = this.onlyData;
      const activeArr = activeData.map(item => {
        return { attr_id: item.attr_id, attr_value: item.attr_vals };
      });
      const staticArr = staticData.map(item => {
        return { attr_id: item.attr_id, attr_value: item.attr_vals };
      });
      this.form.attrs = [...activeArr, ...staticArr];
      const res = await this.$http.post(`goods`, this.form);
      const {
        meta: { msg, status }
      } = res.data;
      if (status === 201) {
        this.$router.push({
          name: "goods"
        });
        this.$message.success(msg);
      }
    }, //
    handleRemove(file) {
      const index = this.form.pics.findIndex(item => {
        return item.pic === file.response.data.tmp_path;
      });
      this.form.pics.splice(index, 1);
    },
    //
    handleSuccess(response) {
      this.form.pics.push({
        pic: response.data.tmp_path
      });
    },

    //tab 
    async changeTab() {
      //
      if (this.active === "2") {
        //
        if (this.selectedOptions.length === 3) {
          const res = await this.$http.get(
            `categories/${this.selectedOptions[2]}/attributes?sel=many`
          );
          this.manyData = res.data.data;
          this.manyData.forEach(item => {
            item.attr_vals =
              item.attr_vals.trim().length === 0
                ? []
                : item.attr_vals.trim().split(",");
          });
        } else {
          this.$message.warning("");
        }
      }
      //
      if (this.active === "3") {
        //
        if (this.selectedOptions.length === 3) {
          const res = await this.$http.get(
            `categories/${this.selectedOptions[2]}/attributes?sel=only`
          );
          this.onlyData = res.data.data;
        } else {
          this.$message.warning("");
        }
      }
    },
    //
    async getGoodsCate() {
      const res = await this.$http.get(`categories`);
      this.options = res.data.data;
    },
    // 
    handleChange(value) {}
  },
  created() {
    this.getGoodsCate();
  }
};
</script>

<style scoped>
.card {
  height: 100%;
}
.row {
  margin: 20px 0;
}
.el-tab-pane {
  height: 400px;
  overflow: auto;
}
.ql-container {
  height: 200px;
}
</style>



